.favorites {
  width: 100%;
  margin: 0 auto;
}

.fav-container {
  flex-direction: column;
  gap: 40px;
  margin: 0 auto;
  display: flex;
}

.fav-hero {
  flex-direction: column;
  gap: 40px;
  display: flex;
}

.fav-image {
  width: 100%;
  height: 100%;
  display: flex;
}

.thumb {
  border-radius: 3%;
  overflow: hidden;
}

.block-for-empty {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  margin: 0 auto;
  display: flex;
}

.message-for-empty {
  color: var(--txt-color);
  width: 100%;
  font-size: 14px;
  line-height: 1.28;
}

.fav-category-fltr-btn {
  border: 1px solid var(--gray-color-btn);
  color: var(--gray-color-btn);
  background-color: var(--bg-main);
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  gap: 18px;
  padding: 14px 18px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  display: flex;
}

.fav-category-fltr-btn:hover, .fav-category-fltr-btn:focus {
  background-color: var(--accent);
  color: var(--white_2);
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.recipes-block {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

@media screen and (min-width: 375px) {
  .fav-container {
    width: 335px;
    padding: 0 20px;
  }

  .fav-image {
    width: 335px;
    height: 516px;
  }

  .message-for-empty {
    width: 335px;
  }
}

@media screen and (min-width: 768px) {
  .fav-container {
    gap: 64px;
    width: 704px;
    padding: 0 32px;
  }

  .fav-hero {
    gap: 64px;
  }

  .fav-image {
    width: 704px;
    height: 356px;
  }

  .recipe-icon-pic {
    width: 97px;
    height: 83px;
  }

  .block-for-empty {
    gap: 32px;
  }

  .message-for-empty {
    width: 481px;
    font-size: 16px;
    line-height: 1.5;
  }

  .recipes-block {
    gap: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .fav-container {
    width: 1080px;
    padding: 0 100px;
  }

  .fav-image {
    width: 1080px;
    height: 356px;
  }
}

